<template>
  <div class="login-page">
    <van-nav-bar title="面筋登陆" />
    <van-form @submit="onSubmit">
      <van-field
        v-model="username"
        name="username"
        label="用户名"
        placeholder="用户名"
        :rules="[{ required: true, message: '请填写用户名' },
        {pattern:/\w{3,}/,message:'用户名至少3位'}]"
      />
      <van-field
        v-model="password"
        type="password"
        name="password"
        label="密码"
        placeholder="密码"
        :rules="[{ required: true, message: '请填写密码' },
        {pattern:/^\w{6,}/,message:'密码需要包含大小写数字'}]"
      />
      <div style="margin: 16px">
        <van-button  block type="info" native-type="submit"
          >登陆</van-button
        >
      </div>
    </van-form>
    <router-link class="link" to="/register">注册账号</router-link>
  </div>
</template>

<script>
export default {
  name: 'login-page',
  data () {
    return {
      username: '',
      password: ''
    }
  },
  methods: {
    onSubmit (values) {
      console.log('submit', values)
    }
  }
}
</script>

<style lang="less" scoped>
.link {
  color: #069;
  font-size: 12px;
  padding-right: 20px;
  float: right;
}
</style>
